"use client";

import { Button } from "@/components/ui/button";
import { motion } from "framer-motion";
import { Shield, Zap, Clock, TrendingUp } from "lucide-react";
import { useState } from "react";
import OfferModal from "./OfferModal";
import { supabase } from "@/lib/supabaseClient";

interface Lead {
    id: string;
    title: string;
    brand: string;
    mileage: number;
    registration_date: string;
    price_standard: number;
    price_exclusive: number;
    status: string;
}

export default function LeadCard({ lead }: { lead: Lead }) {
    const [isOfferOpen, setIsOfferOpen] = useState(false);
    const [isBuying, setIsBuying] = useState(false);

    const handleExclusiveBuy = async () => {
        if (!confirm(`Confirm Exclusive Buy for €${lead.price_exclusive}? This will remove it for everyone else.`)) return;
        setIsBuying(true);

        try {
            // 1. Record purchase (mock)
            // await supabase.from('purchases').insert(...)

            // 2. Update status to 'sold_exclusive'
            // RLS should allow this update if we were really logged in and policy allowed it, 
            // or we use a server action with admin privs. 
            // For this demo, assuming public write access or logged in user policy.
            const { error } = await supabase
                .from('leads')
                .update({ status: 'sold_exclusive' })
                .eq('id', lead.id);

            if (error) {
                console.error("Error buying exclusive:", error);
                alert("Failed to purchase. It may have been sold.");
            }
        } catch (err) {
            console.error(err);
        } finally {
            setIsBuying(false);
        }
    };

    const handleStandardBuy = () => {
        alert(`Standard buy initiated for €${lead.price_standard}. You would be redirected to stripe/checkout.`);
    };

    return (
        <>
            <motion.div
                initial={{ opacity: 0, scale: 0.95 }}
                animate={{ opacity: 1, scale: 1 }}
                className="glass-card p-0 flex flex-col justify-between h-full group relative overflow-hidden"
            >
                {/* Glow Effect on Hover */}
                <div className="absolute inset-0 bg-primary/5 opacity-0 group-hover:opacity-100 transition-opacity duration-500 pointer-events-none" />

                <div className="p-6 space-y-4">
                    <div className="flex justify-between items-start">
                        <div>
                            <span className="inline-block px-2 py-1 bg-white/5 border border-white/10 rounded text-[10px] uppercase tracking-widest text-muted-foreground mb-2">
                                {lead.brand}
                            </span>
                            <h3 className="text-lg font-bold text-white group-hover:text-primary transition-colors">
                                {lead.title}
                            </h3>
                        </div>
                        <div className="text-right">
                            <Zap className="w-5 h-5 text-primary opacity-50" />
                        </div>
                    </div>

                    <div className="grid grid-cols-2 gap-4 py-4 border-y border-white/5">
                        <div className="flex flex-col gap-1">
                            <div className="flex items-center gap-1.5 text-xs text-muted-foreground">
                                <TrendingUp className="w-3 h-3" /> Mileage
                            </div>
                            <span className="text-sm font-medium">{lead.mileage.toLocaleString()} km</span>
                        </div>
                        <div className="flex flex-col gap-1">
                            <div className="flex items-center gap-1.5 text-xs text-muted-foreground">
                                <Clock className="w-3 h-3" /> Registration
                            </div>
                            <span className="text-sm font-medium">{new Date(lead.registration_date).getFullYear()}</span>
                        </div>
                    </div>

                    <div className="space-y-3 pt-2">

                        {/* Standard Option */}
                        <div onClick={handleStandardBuy} className="flex items-center justify-between group/std p-2 rounded-lg hover:bg-white/5 transition-colors cursor-pointer border border-transparent hover:border-white/5">
                            <div className="flex items-center gap-3">
                                <div className="w-8 h-8 rounded-full bg-white/5 flex items-center justify-center text-muted-foreground group-hover/std:text-white transition-colors">
                                    1
                                </div>
                                <div className="flex flex-col">
                                    <span className="text-xs text-muted-foreground">Standard Buy</span>
                                    <span className="font-semibold text-white">€{lead.price_standard}</span>
                                </div>
                            </div>
                            <Button size="sm" variant="outline" className="opacity-0 group-hover/std:opacity-100 transition-opacity">
                                Buy
                            </Button>
                        </div>

                        {/* Exclusive Option */}
                        <div onClick={handleExclusiveBuy} className="flex items-center justify-between group/excl p-2 rounded-lg bg-primary/5 border border-primary/10 hover:border-primary/50 transition-colors cursor-pointer relative overflow-hidden">

                            {/* Shimmer effect */}
                            <div className="absolute inset-0 bg-gradient-to-r from-transparent via-primary/5 to-transparent -translate-x-full group-hover/excl:animate-shimmer" />

                            <div className="flex items-center gap-3 relative z-10">
                                <div className="w-8 h-8 rounded-full bg-primary/20 flex items-center justify-center text-primary">
                                    <Shield className="w-4 h-4" />
                                </div>
                                <div className="flex flex-col">
                                    <span className="text-xs text-primary font-medium tracking-wide">EXCLUSIVE</span>
                                    <span className="font-bold text-white">€{lead.price_exclusive}</span>
                                </div>
                            </div>
                            <Button size="sm" variant="gold" className="relative z-10" disabled={isBuying}>
                                {isBuying ? "Processing..." : "Buy Exclusive"}
                            </Button>
                        </div>

                    </div>
                </div>

                <div className="p-4 bg-white/3 border-t border-white/5 text-center">
                    <button
                        onClick={() => setIsOfferOpen(true)}
                        className="text-xs text-muted-foreground hover:text-white transition-colors underline-offset-4 hover:underline"
                    >
                        Make an offer
                    </button>
                </div>

            </motion.div>

            <OfferModal
                isOpen={isOfferOpen}
                onClose={() => setIsOfferOpen(false)}
                leadTitle={lead.title}
                leadId={lead.id}
            />
        </>
    );
}
