"use client";

import { useEffect, useState } from "react";
import Filters from "@/components/Filters";
import LeadCard from "@/components/LeadCard";
import { supabase } from "@/lib/supabaseClient";

// Dummy data for initial view before DB fetch
const DUMMY_LEADS = [
    { id: '1', title: 'Audi RS6 Avant', brand: 'Audi', mileage: 12000, registration_date: '2023-05-01', price_standard: 500, price_exclusive: 5000, status: 'available' },
    { id: '2', title: 'Mercedes G63 AMG', brand: 'Mercedes', mileage: 5000, registration_date: '2024-01-15', price_standard: 800, price_exclusive: 8000, status: 'available' },
    { id: '3', title: 'Porsche 911 GT3', brand: 'Porsche', mileage: 2500, registration_date: '2023-11-20', price_standard: 1000, price_exclusive: 10000, status: 'available' },
    { id: '4', title: 'Lamborghini Urus', brand: 'Lamborghini', mileage: 8500, registration_date: '2022-08-10', price_standard: 1200, price_exclusive: 12000, status: 'available' },
];

export default function Dashboard() {
    const [leads, setLeads] = useState(DUMMY_LEADS);

    useEffect(() => {
        // Initial Fetch
        const fetchLeads = async () => {
            const { data } = await supabase.from('leads').select('*').eq('status', 'available');
            if (data && data.length > 0) {
                // Merge with dummy or replace. For now replacing if DB has data.
                // Note: In real app, remove dummy data.
                setLeads(data as any);
            }
        };

        fetchLeads();

        // Realtime Subscription
        const channel = supabase
            .channel('leads-changes')
            .on('postgres_changes', { event: 'UPDATE', schema: 'public', table: 'leads' }, (payload) => {
                if (payload.new.status === 'sold_exclusive') {
                    // Remove from list instantly
                    setLeads(current => current.filter(lead => lead.id !== payload.new.id));
                }
            })
            .subscribe();

        return () => {
            supabase.removeChannel(channel);
        };
    }, []);

    return (
        <div className="min-h-screen pt-20 pb-10 px-4 md:px-8 max-w-[1600px] mx-auto">
            <div className="flex flex-col lg:flex-row gap-8">
                <Filters />

                <div className="flex-1">
                    <div className="mb-6 flex items-center justify-between">
                        <h2 className="text-2xl font-semibold tracking-tight">Marketplace <span className="text-muted-foreground text-sm font-normal ml-2">{leads.length} Active Leads</span></h2>
                        {/* Sort Dropdown could go here */}
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
                        {leads.map((lead) => (
                            <LeadCard key={lead.id} lead={lead} />
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
}
